<template>
  <div>
    <div>
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title>共有幾個{{parent_name}}：</v-list-item-title>
          <v-select
            v-model="parent_num"
            item-text="item_id"
            item-value="item_value"
            :items="parent_range"
            :label="'請選擇有幾個'+parent_name"
            solo
          ></v-select>
          <input type="hidden" :name="parent_who+'_n'" :value="parent_num" />
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>
    </div>

    <div>
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title>共有幾個{{parent_name}}罹癌：</v-list-item-title>
          <v-select
            v-model="cancer_num"
            item-text="itemcancer_id"
            item-value="itemcancer_value"
            :items="cancer_range"
            :label="'請選擇有幾個'+parent_name+'罹癌'"
            solo
          ></v-select>
          <input type="hidden" :name="parent_who" :value="cancer_num" />
        
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>
    </div>

    <div v-for="i in cancer_num" :key="i">
      <v-divider></v-divider>
      <ParentDetail
        :i="i"
        :degree="degree"
        :parent="parent"
        :parent_name="parent_name"
        :parent_who="parent_who"
      ></ParentDetail>
    </div>
  </div>
</template>
<script>
import ParentDetail from "@/components/ParentDetail.vue";

export default {
  components: {
    ParentDetail
  },
  props: {
    degree: String,
    parent: String,
    parent_name: String,
    parent_who: String
  },

  data() {
    return {
      parent_range: [],
      cancer_range: [],
      parent_num: 0,
      cancer_num: 0,
    };
  },
  methods: {
    parent_range_function() {
      var itms = [];
      var i;
      for (i = 0; i < 25; i++) {
        itms.push({ item_id: i, item_value: i });
      }
      this.parent_range = itms;
    },
    cancer_range_function() {
      //parent_num
      var itms = [];
      var i;
      for (i = 0; i < 5; i++) {
        itms.push({ itemcancer_id: i, itemcancer_value: i });
      }
      this.cancer_range = itms; 
    }
  },

  mounted() {
    this.parent_range_function();
    this.cancer_range_function();
  }
};
</script>